.rotatez{
        -webkit-animation:rotatez 1s linear infinite;
                animation:rotatez 1s linear infinite;
    }
.flicker{
        -webkit-animation:flicker .9s linear infinite;
                animation:flicker .9s linear infinite;
    }
@-webkit-keyframes rotatez {
        from{
           -webkit-transform: rotateZ(0deg);
                   transform: rotateZ(0deg)
        }

        to{
            -webkit-transform: rotateZ(360deg);
                    transform: rotateZ(360deg)
        }
    }
@keyframes rotatez {
        from{
           -webkit-transform: rotateZ(0deg);
                   transform: rotateZ(0deg)
        }

        to{
            -webkit-transform: rotateZ(360deg);
                    transform: rotateZ(360deg)
        }
    }
@-webkit-keyframes jump {
        from{
           -webkit-transform: translateY(0);
                   transform: translateY(0)
        }
        50%{
            -webkit-transform: translateY(5px);
                    transform: translateY(5px)
         }
        to{
            -webkit-transform: translateY(0);
                    transform: translateY(0)
        }
    }
@keyframes jump {
        from{
           -webkit-transform: translateY(0);
                   transform: translateY(0)
        }
        50%{
            -webkit-transform: translateY(5px);
                    transform: translateY(5px)
         }
        to{
            -webkit-transform: translateY(0);
                    transform: translateY(0)
        }
    }
@-webkit-keyframes flicker {
        from{
            -webkit-box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.9) inset;
                    box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.9) inset
        }
        50%{
            -webkit-box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.0) inset;
                    box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.0) inset
            }
        to{
            -webkit-box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.9) inset;
                    box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.9) inset
        }
    }
@keyframes flicker {
        from{
            -webkit-box-shadow: 0px 0px 5vw 5px rgba(252,238,119,.9) inset;
